  background-size: 100% 100%;
}
.custom-radio > input:not(:disabled):not(:checked) + span:hover::before {
  background: none;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16px 16px'%3e%3ccircle cx='50%' cy='50%' r='8' fill='%237859cf40'/%3e%3c/svg%3e");
  border: 2px solid #9070ec;
}
.custom-radio > input:checked + span::before {
  border: 2px solid #7859cf;
  background-color: #ffffff;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16px 16px'%3e%3ccircle cx='50%' cy='50%' r='8' fill='%237859cf'/%3e%3c/svg%3e");
