  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16px 16px'%3e%3ccircle cx='50%' cy='50%' r='8' fill='%23e2e2e2'/%3e%3c/svg%3e");
}
.custom-radio > input:disabled + span::before {
  border: 2px solid #e2e2e2;
}
.custom-radio > input:disabled + span.text {
  color: #bdbdbd;
}
.custom-checkbox {
  cursor: pointer;
  position: relative;
  display: inline-block;
  width: 42px;
  height: 24px;
  margin-top: 15px;
}
.custom-checkbox input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.custom-checkbox > span {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #bdbdbd;
  border: 2px solid #bdbdbd;
  border-radius: 12px;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  display: inline-flex;
}
.custom-checkbox > span:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  background-color: #ffffff;
  border-radius: 50%;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  margin-right: 0.5em;
}
.custom-checkbox > input:not(:disabled):not(:checked) + span:hover {
  background-color: #7859cf40;
  border: 2px solid #9070ec;
}
.custom-checkbox > input:checked + span {
  background-color: #7859cf;
  border: 2px solid #7859cf;
}
.custom-checkbox > input:checked + span::before {
  -webkit-transform: translateX(18px);
  -ms-transform: translateX(18px);
  transform: translateX(18px);
}
.custom-checkbox > input:checked:disabled + span::before {
  background-color: #bdbdbd;
}
.custom-checkbox > input:disabled + span {
  background-color: #e2e2e2;
  border: 2px solid #e2e2e2;
}
.custom-checkbox > input:disabled + span::before {
  background-color: #f8f8f8;
}
.custom-checkbox > input:disabled + span.text {
  color: #bdbdbd;
}
